<script lang="ts">
import { computed, defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import Header from '@/components/Header.vue'
import { faChevronRight, faLanguage, faList, faMedal, faPlus } from '@fortawesome/free-solid-svg-icons'
import { faChromecast } from '@fortawesome/free-brands-svg-icons'

export default defineComponent({
  components: {
    Header
  },
  setup() {
    const store = useStore()
    const state = ref(store.state)
    const setLoading = (value: boolean) => {
      store.commit('setLoading', value)
    }
    setLoading(true)

    const castIcon = computed(() => faChromecast)
    const languageIcon = computed(() => faLanguage)
    const arrowRight = computed(() => faChevronRight)
    const listIcon = computed(() => faList)
    const medalIcon = computed(() => faMedal)
    const plusIcon = computed(() => faPlus)

    return {
      castIcon,
      languageIcon,
      arrowRight,
      listIcon,
      medalIcon,
      plusIcon,
      setLoading,
      state
    }
  }
})
</script>

